import { Modal } from "antd";


const { confirm } = Modal;

export function formatMoney(amount, decimalCount = 2, decimal = ".", thousands = ",") {
  try {
    decimalCount = Math.abs(decimalCount);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

    const negativeSign = amount < 0 ? "-" : "";

    let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
    let j = (i.length > 3) ? i.length % 3 : 0;

    return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
  } catch (e) {
    console.log(e)
  }
}

export function formatNumber(num) {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}

export function checkVersion() {
  fetch(`/stocks-watchlist/index.html?v=${process.env.REACT_APP_VERSION}`, { cache: "no-cache" })
    .then(function (res) {
      return res.text();
    })
    .then(function (body) {
      const regex = /main.(\w+).(\w+).js/gm;
      const match = body.match(regex);
      if (match) {
        const new_hash = match[0].split(".")[1];

        // Check and compare hash then reload
        const old_hash = localStorage.getItem("hash_version");
        if (old_hash) {
          if (new_hash !== old_hash) {
            // Mark is checking
            localStorage.setItem("checking_version", true);

            // Show popup notify new version need reload
            confirm({
              title: "Có phiên bản mới cập nhật",
              content:
                "Nhấn vào nút OK để cập nhật, nhấn cancel để bỏ qua!",
              onOk() {
                // Remove is checking
                localStorage.removeItem("checking_version");

                // Store new hash
                localStorage.setItem("hash_version", new_hash);

                // Load new version
                window.location.href = `/stocks-watchlist/index.html?v=${process.env.REACT_APP_VERSION}`;
              },
              onCancel() {
                // Remove is checking
                localStorage.removeItem("checking_version");
              },
            });
          }
        } else {
          // Store init hash
          localStorage.setItem("hash_version", new_hash);
        }
      }
    });
}