import React from "react";
import { Popconfirm } from "antd";
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';
import { formatMoney } from "./utils";

export const getColumns = (onEditRow, onDeleteRow) => [
  {
    title: "Mã",
    dataIndex: "code",
    key: "code",
    fixed: "left",
    width: 80,
  },
  {
    title: "Cut loss",
    dataIndex: "cut_loss",
    key: "cut_loss",
  },
  {
    title: "Khoảng mua",
    dataIndex: "range_buy",
    key: "range_buy",
    render: (text, record) => (
      <span>{record.buyFrom} - {record.buyTo}</span>
    )
  },
  {
    title: "Số lượng mua",
    dataIndex: "buy_number",
    key: "buy_number",
  },
  {
    title: "Target",
    dataIndex: "target",
    key: "target",
  },
  {
    title: "Lợi nhuận",
    dataIndex: "profit",
    key: "profit",
    render: (text, record) => (
      record.target && record.buyTo
        ? (
          ((record.target - record.buyTo) * 100) /
          record.buyTo
        ).toFixed(2) + '%'
        : ""
    )
  },
  {
    title: "Tiền lời",
    dataIndex: "revenue",
    key: "revenue",
    render: (text, record) => (
      record.target &&
      record.buyTo &&
      record.buy_number &&
      formatMoney(
        record.buyTo *
        record.buy_number *
        (((record.target - record.buyTo) *
          100) /
          record.buyTo) *
        10,
        0
      )
    )
  },
  {
    title: "Hành động",
    dataIndex: "action",
    key: "action",
    fixed: "right",
    width: 120,
    render: (text, record) => (
      <div>
        <EditOutlined style={{ paddingRight: 10 }} onClick={() => onEditRow(record)} />
        <Popconfirm placement="topRight" title="Bạn có chắc chắn muốn xoá mã này không?" onConfirm={() => onDeleteRow(record)}>
          <DeleteOutlined style={{ paddingLeft: 10 }} />
        </Popconfirm>
      </div>
    )
  },
];